<template>
	<div>
		<!-- 		顶部跳过按钮
		<div class="header">
			<button class="skip-button" @click="skipPage">跳过</button>
		</div> -->

		<!-- 用于加载指定网页的iframe -->
		<iframe :src="iframeSrc" title="浏览网页"></iframe>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				iframeSrc: "https://sugar.aipage.com/dashboard/69b8e3bf9dc4d83d8e3a2e5be68e1e1a" // 默认加载百度
			};
		},
		methods: {
			back_to_draw() {
				uni.showModal({
					title: '提示',
					content: '请进入下一阶段',
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							uni.redirectTo({
								url: '/pages/new_draw/new_draw?stage=3'
							})
						}
					}
				});
			}
		},
		mounted() {
			// 设置定时器，2秒后调用 back_to_draw 方法
			setTimeout(() => {
				this.back_to_draw();
			}, 20000); // 2000 毫秒（2秒）后执行
		}
		// 禁用返回键关闭弹窗

	};
</script>

<style scoped>
	@import './watch_data.css'
</style>